import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import { getTime, getDate} from '@/common/js/unti.js'
export default {
	components:{
		mpvuePicker
	},
	data() {
		return {
			defaultAlist: [0],
			defaultTolist: [0],
			defaultThreelist: [0],
			pickerValueOfAlist: [{
				value: 0,
				label: '中国'
			},{
				value: 1,
				label: '美国'
			}],
			pickerValueOfTolist: [{
				value: 0,
				label: '初中',
				children: [{
					value: 0,
					label: '初一'
				},{
					value: 1,
					label: '初二'
				},{
					value: 2,
					label: '初三'
				}]
			},{
				value: 1,
				label: '高中',
				children: [{
					value: 0,
					label: '高一'
				},{
					value: 1,
					label: '高二'
				},{
					value: 2,
					label: '高三'
				}]
			}],
			pickerValueOfThreelist: [{
				value: '0',
				label: '安徽',
				children: [{
					value: '0',
					label: '蚌埠',
					children: [{
						value: '0',
						label: '怀远'
					},{
						value: '1',
						label: '五河'
					}]
				},{
					value: '1',
					label: '滁州',
					children: [{
						value: '0',
						label: '定远'
					}]
				}]
			}],
			time: getTime(),
			date: getDate()
		}
	},
	methods: {
		fnAListOf () {
			this.defaultAlist = [0];
			this.$refs.onePicker.show();
		},
		onConfirmAlist(e) {
			console.log(e);
			uni.showToast({
				title: e.label,
				icon: 'none'
			})
		},
		fnToListOf () {
			this.defaultTolist = [0, 0];
			this.$refs.twoPicker.show();
		},
		onConfirmTolist (e) {
			console.log(e);
			uni.showToast({
				title: e.label,
				icon: 'none'
			})
		},
		fnThreeListOf () {
			this.defaultThreelist = [0, 0, 0];
			this.$refs.threePicker.show();
		},
		onConfirmThreelist (e) {
