			uni.showToast({
				title: e.label,
				icon: 'none'
			})
		},
		bindTimeChange (event) {
			console.log(event);
			uni.showToast({
				title: event.detail.value,
				icon: 'none'
			})
		},
		bindCancelTime () {
			console.log('@@@@@关闭时间选择器@@@@@@@');
		},
		bindDateChange (event) {
			console.log(event);
			uni.showToast({
				title: event.detail.value,
				icon: 'none'
			})
		},
		bindCancelDate () {
			console.log('@@@@@关闭日期选择器@@@@@@@')
		}
	}
}
